@import '../variables.css';

.container-switcher{
    display: flex;
    flex-direction: row;
    height: 28px;
    align-items: center;
    margin-bottom: 14px;
    justify-content: flex-end;
}

.label-switcher{
    padding-right: 10px;
    width: auto;
}

.switch {
    position: relative;
    display : inline-block;
    width   : 50px;
    height  : 100%;
}

.switch input {
    opacity: 0;
    width  : 0;
    height : 0;
}

.slider {
    position          : absolute;
    cursor            : pointer;
    top               : 0;
    left              : 0;
    right             : 0;
    bottom            : 0;
    background-color  : var(--theme-page-block);
    -webkit-transition: .4s;
    transition        : .4s;
}

.slider:before {
    position          : absolute;
    content           : "";
    height            : 20px;
    width             : 20px;
    left              : 4px;
    bottom            : 4px;
    background-color  : white;
    -webkit-transition: .4s;
    transition        : .4s;
}

input:checked+.slider {
    background-color: var(--theme-page-icon);
}

input:checked+.slider:before {
    -webkit-transform: translateX(20px);
    -ms-transform    : translateX(20px);
    transform        : translateX(20px);
}

/* Rounded sliders */
.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}